import Vue from 'vue'

import App from './App'
import Hello from './components/Hello'
import Form from './components/Form'
import VueRouter from 'vue-router'
Vue.use(VueRouter)

const router = new VueRouter()

router.map({
    '/index': {
        component: App
    }
})

// router.redirect({
//     '*': '/hello'
// })
router.start(App, '#app')

/* var App  */
new Vue({
    el: '#app',
    data: {
        currentView: 'index'
    }
})



