<style scoped>
    h1 {
        color: #42b983;
    }
</style>

<template>
    <div class="hello">
        <p>hello start</p>
        <h1>{{ msg || 'Hello World!' }}</h1>
        <input type="text" v-model="msg" lazy>
    </div>
    <p>hello end</p>
</template>

<script>
    export default {
        name: 'Hello',
        data() {
            return {
                // note: changing this line won't causes changes
                // with hot-reload because the reloaded component
                // preserves its current state and we are modifying
                // its initial state.
                msg: ''
            }
        }
    }
</script>



